.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #3b4244, #1e1f1f);
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App-header {
  max-width: 90%;
  padding: 20px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.App-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.App-description {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.start-btn {
  background-color: #ffffff;
  color: #232526;
  border: none;
  padding: 15px 30px;
  font-size: 1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s ease;
}

.start-btn:hover {
  background-color: #f0f0f0;
  transform: scale(1.05);
}

.quiz-container {
  padding: 20px;
  max-width: 800px;
  margin: 40px auto;
  width: 100%;
}

.question-card {
  background-color: #ffffff;
  color: #333;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  text-align: left;
}

.question-card h3 {
  margin-bottom: 10px;
}

.question-card ul {
  list-style: none;
  padding: 0;
}

.question-card li {
  margin-bottom: 8px;
}

.submit-btn {
  background-color: #0083b0;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 10px;
}

.submit-btn:disabled {
  background-color: gray;
  cursor: not-allowed;
}

.result {
  margin-top: 15px;
  padding: 10px;
  border-radius: 8px;
}

.correct {
  background-color: #d4edda;
  color: #155724;
}

.wrong {
  background-color: #f8d7da;
  color: #721c24;
}

.description {
  margin-top: 8px;
  font-style: italic;
}

@keyframes fadeInOut {
  0% { opacity: 0; transform: scale(0.95) translate(-50%, -50%); }
  10% { opacity: 1; transform: scale(1) translate(-50%, -50%); }
  90% { opacity: 1; transform: scale(1) translate(-50%, -50%); }
  100% { opacity: 0; transform: scale(0.95) translate(-50%, -50%); }
}

.ribbon {
  position: fixed;
  width: 100px;
  height: 10px;
  top: -20px;
  opacity: 0.8;
  z-index: 9999;
  border-radius: 10px;
  animation: ribbonFall 3s linear infinite;
}

.ribbon-0 { left: 10%; background: linear-gradient(90deg, red, orange); animation-delay: 0s; }
.ribbon-1 { left: 30%; background: linear-gradient(90deg, blue, cyan); animation-delay: 0.3s; }
.ribbon-2 { left: 50%; background: linear-gradient(90deg, green, lime); animation-delay: 0.6s; }
.ribbon-3 { left: 70%; background: linear-gradient(90deg, purple, pink); animation-delay: 0.9s; }
.ribbon-4 { left: 90%; background: linear-gradient(90deg, yellow, gold); animation-delay: 1.2s; }

@keyframes ribbonFall {
  0% {
    top: -20px;
    transform: rotateZ(0deg) translateX(0);
    opacity: 1;
  }
  100% {
    top: 110%;
    transform: rotateZ(360deg) translateX(50px);
    opacity: 0;
  }
}
